.app-wrapper {
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
          'h n'
          'c c';
    grid-template-rows: 70px 1fr;
    grid-template-columns: 2fr 10fr;
}
.app-wrapper-content {
    grid-area: c;
    background-color: #EAD5E6;
    min-height: 100vh;
}
